import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import SectionDisplayWrapper from '../SectionDisplayWrapper';

type Props = {
  display?: string;
  className?: string;
  name?: string;
};

const SectionDisplay = ({
  className,
  display,
  name,
}: Props): React.ReactElement => {
  const displayImage = useStaticQuery(
    graphql`
      {
        store: file(relativePath: { eq: "store.png" }) {
          childImageSharp {
            gatsbyImageData(quality: 90, placeholder: NONE, layout: CONSTRAINED)
          }
        }
        nfts: file(relativePath: { eq: "nfts.png" }) {
          childImageSharp {
            gatsbyImageData(quality: 90, placeholder: NONE, layout: CONSTRAINED)
          }
        }
        auctions: file(relativePath: { eq: "auctions.png" }) {
          childImageSharp {
            gatsbyImageData(quality: 90, placeholder: NONE, layout: CONSTRAINED)
          }
        }
        collab: file(relativePath: { eq: "collab.png" }) {
          childImageSharp {
            gatsbyImageData(quality: 90, placeholder: NONE, layout: CONSTRAINED)
          }
        }
      }
    `,
  );

  return (
    <SectionDisplayWrapper className={className}>
      {display === null || display === '' ? (
        <GatsbyImage image={getImage(displayImage[name])} alt={name} />
      ) : (
        <div
          dangerouslySetInnerHTML={{
            __html: display,
          }}
        />
      )}
    </SectionDisplayWrapper>
  );
};

export default SectionDisplay;
