import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import tw, { styled } from 'twin.macro';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';

const OuterContainer = tw.div`
    overflow-hidden
`;

const InnerContainer = styled.div`
  ${tw`
    relative
    flex flex-col justify-center items-center
    text-white
  `}
  transform: (45deg);
  padding-top: 3rem;
  padding-bottom: 6rem;
  &:after,
  &:before {
    content: '';
    position: absolute;
    transform: rotate(-45deg);
    width: 500% !important;
    height: 500% !important;
    top: -200% !important;
    left: -200% !important;
    z-index: -1;
  }
  h3 {
    font-size: 3rem;
    line-height: 4rem;
    font-weight: 700;
  }
  h5 {
    ${tw`text-gray40`}
    margin-bottom: 1rem;
  }
`;

const DropWrapper = tw.div`
  flex flex-col
  justify-center items-center
  lg:grid grid-cols-3 gap-5
  place-content-center
  mt-4
`;

const Drop = styled.a`
  ${tw`
  relative
  rounded-lg
  mx-2
  transform-gpu
  transition
  duration-150
  hover:scale-105
  `}
`;

const DropImage = tw(GatsbyImage)`
  relative
  inset-0
  w-full
`;

const DropImageCryptoKickers = tw(GatsbyImage)`
  relative
  inset-0
  w-full
`;

const DropText = styled.span`
  ${tw`
    absolute
    // text-xl
    bottom-10
    left-10
    z-10
  `}
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 500;
`;

/**
 * The Welcome Section.
 *
 * @constructor
 */
const WelcomeSection = (): React.ReactElement => {
  const { welcome, onoImage, ckImage, sdImage } = useStaticQuery(
    graphql`
      query {
        welcome: markdownRemark(frontmatter: { slug: { eq: "welcome" } }) {
          frontmatter {
            title
            subtitle
            drop1
            drop2
            drop3
          }
        }
        onoImage: file(relativePath: { eq: "welcomeono.png" }) {
          childImageSharp {
            gatsbyImageData(
              quality: 90
              placeholder: BLURRED
              layout: CONSTRAINED
            )
          }
        }
        ckImage: file(relativePath: { eq: "welcomecryptokickers.png" }) {
          childImageSharp {
            gatsbyImageData(
              quality: 90
              placeholder: BLURRED
              layout: CONSTRAINED
            )
          }
        }
        sdImage: file(relativePath: { eq: "welcomesweetdreams.png" }) {
          childImageSharp {
            gatsbyImageData(
              quality: 90
              placeholder: BLURRED
              layout: CONSTRAINED
            )
          }
        }
      }
    `,
  );
  const {
    frontmatter: { title, subtitle, drop1, drop2, drop3 },
  } = welcome;
  const onoImageData = getImage(onoImage);
  const ckImageData = getImage(ckImage);
  const sdImageData = getImage(sdImage);
  return (
    <OuterContainer>
      <InnerContainer>
        <h3>{title}</h3>
        <h5>{subtitle}</h5>
        <DropWrapper>
          <Drop href="https://ono.art">
            <DropImage alt={drop1} image={onoImageData} />
            <DropText>{drop1}</DropText>
          </Drop>
          <Drop href="https://streetdreams.co/cafe/">
            <DropImage alt={drop2} image={sdImageData} />
            <DropText>{drop2}</DropText>
          </Drop>
          <Drop href="https://www.cryptokickers.com/">
            <DropImageCryptoKickers alt={drop3} image={ckImageData} />
            <DropText>{drop3}</DropText>
          </Drop>
        </DropWrapper>
      </InnerContainer>
    </OuterContainer>
  );
};

export default WelcomeSection;
