import * as React from 'react';
import tw, { styled } from 'twin.macro';

import SectionDisplay from './SectionDisplay';

type SectionProps = {
  backgroundColor?: string;
};

const SectionOuterWrapper = styled.div<SectionProps>`
  ${tw`
    min-h-halfScreen w-full container-2xl
    py-5 md:py-20 text-base
  `}
  h3 {
    ${tw`px-5 md:px-10`}
  }
  p {
    ${tw`px-5 md:px-10`}
  }
  a {
    ${tw`
      text-btnMetaPurple
      border-b-2 border-transparent hover:border-b-2 hover:border-btnMetaPurple
    `}
  }
  .right {
    ${tw`text-right`}
  }
  background-color: ${props =>
    props.backgroundColor === 'light' ? `#121212` : `transparent`};
`;

const SectionWrapperLeft = tw.div`
  flex flex-col-reverse md:flex-row items-center
`;

const SectionWrapperRight = tw.div`
  flex flex-col md:flex-row items-center
`;

const ContainerText = styled.div`
  ${tw`
    w-full md:w-1/2
    text-white text-left text-base
  `}
`;

const ContainerDisplay = tw.div`
  w-full md:w-1/2
  flex flex-row items-center justify-center
  text-white text-base
  pb-10 md:pb-0
`;

const RoundedSectionDisplay = styled(SectionDisplay)`
  ${tw`rounded-xl`}
  max-width: 90%;
`;

const SectionBgGradient = styled(RoundedSectionDisplay)`
  ${tw`flex items-end`}
  background: linear-gradient(135deg, #eca572 0%, #155fce 100%);
  backdrop-filter: blur(600px);
`;

const SectionBgColor = styled(RoundedSectionDisplay)`
  background: #ad7bbc;
`;

const SectionBgDark = tw(RoundedSectionDisplay)`
  bg-baseBg
  p-10
  items-center
  text-center
  text-white
`;

type ContainerSwitchProps = {
  name: string;
  display?: string;
  imageWrapper?: string;
};

const ContainerSwitch = ({
  imageWrapper,
  name,
  display,
}: ContainerSwitchProps) => (
  <ContainerDisplay>
    {imageWrapper === 'gradient' ? (
      <SectionBgGradient name={name} display={display} />
    ) : imageWrapper === 'color' ? (
      <SectionBgColor name={name} display={display} />
    ) : imageWrapper === 'dark' ? (
      <SectionBgDark name={name} display={display} />
    ) : (
      <RoundedSectionDisplay name={name} display={display} />
    )}
  </ContainerDisplay>
);

type SingleSectionProps = {
  name: string;
  title?: string;
  position?: string;
  display?: string;
  background?: string;
  imageWrapper?: string;
  html?: string;
};

/**
 * Single Multi Section with position switch.
 *
 * @constructor
 */
const SingleSection = ({
  name,
  title,
  position,
  display,
  background,
  imageWrapper,
  html,
}: SingleSectionProps): React.ReactElement => {
  return (
    <SectionOuterWrapper backgroundColor={background}>
      {position === 'left' ? (
        <SectionWrapperLeft>
          <ContainerText>
            <h3>{title}</h3>
            <h5
              dangerouslySetInnerHTML={{
                __html: html,
              }}
            />
          </ContainerText>
          <ContainerSwitch
            imageWrapper={imageWrapper}
            name={name}
            display={display}
          />
        </SectionWrapperLeft>
      ) : (
        <SectionWrapperRight>
          <ContainerSwitch
            imageWrapper={imageWrapper}
            name={name}
            display={display}
          />
          <ContainerText className="right">
            <h3>{title}</h3>
            <h5
              dangerouslySetInnerHTML={{
                __html: html,
              }}
            />
          </ContainerText>
        </SectionWrapperRight>
      )}
    </SectionOuterWrapper>
  );
};

export default SingleSection;
