import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import tw, { styled } from 'twin.macro';

import MetaPurpleButton from '../MetaPurpleButton';
import { BgImage } from 'gbimage-bridge';
import { getImage } from 'gatsby-plugin-image';

const Container = styled(BgImage)`
  ${tw`
    flex flex-col justify-center items-center
    text-white px-10 md:px-52 pt-20 pb-52
  `}
  p {
    ${tw`w-1/2 text-center text-lg`}
  }
`;

/**
 * Footer section.
 *
 * @constructor
 */
const FooterSection = (): React.ReactElement => {
  const { footer, footerBg } = useStaticQuery(
    graphql`
      query {
        footer: markdownRemark(frontmatter: { slug: { eq: "footer" } }) {
          frontmatter {
            title
            button
          }
          rawMarkdownBody
        }
        footerBg: file(relativePath: { eq: "footer-background.png" }) {
          childImageSharp {
            gatsbyImageData(quality: 90, placeholder: NONE, layout: CONSTRAINED)
          }
        }
      }
    `,
  );
  const footerBgData = getImage(footerBg);
  return (
    <>
      <Container image={footerBgData}>
        <h3>{footer.frontmatter.title}</h3>
        <p>{footer.rawMarkdownBody}</p>
        <MetaPurpleButton>{footer.frontmatter.button}</MetaPurpleButton>
      </Container>
    </>
  );
};

export default FooterSection;
