import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import tw, { styled } from 'twin.macro';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';

const EventSectionWrapper = styled.div`
  ${tw`
  w-full container-2xl
  py-16
  block md:flex flex-row
  `} //background: linear-gradient(270deg, rgba(97, 103, 116, 0.24) 7.29%, rgba(64, 63, 76, 0.24) 100%);
  background: linear-gradient(94.75deg, #141414 0%, #000000 98.82%);
`;

const Container = styled.div`
  ${tw`
    flex flex-col md:flex-row
    items-center
  `}
  padding-left: 10rem;
  h3 {
    ${tw`text-2xl md:text-3xl text-center md:text-left`}
  }
  h4,
  h5 {
    ${tw`normal-case text-center md:text-left`}
  }
`;

const InnerWrapper = styled.div`
  ${tw`
    flex flex-col flex-wrap flex-grow
    text-white
    w-full md:w-1/2
    mt-5 md:ml-11
    md:pr-24
  `}
  font-size: 2.125rem;
  line-height: 3.375rem;
  font-weight: 500;
`;

const Author = styled.h4`
  font-size: 1.75rem;
  line-height: 3rem;
`;

const AuthorDesc = tw.span`
  text-gray40
`;

const RacImage = tw(GatsbyImage)`
  rounded-full
`;

/**
 * Cite section.
 *
 * @constructor
 */
const CiteSection = (): React.ReactElement => {
  const { cite, racImage } = useStaticQuery(
    graphql`
      query {
        cite: markdownRemark(frontmatter: { slug: { eq: "cite" } }) {
          frontmatter {
            title
            summary
          }
          rawMarkdownBody
        }
        racImage: file(relativePath: { eq: "rac.png" }) {
          childImageSharp {
            gatsbyImageData(
              quality: 90
              placeholder: BLURRED
              layout: CONSTRAINED
            )
          }
        }
      }
    `,
  );

  const racImageData = getImage(racImage);
  return (
    <EventSectionWrapper id="event">
      <Container>
        <RacImage
          image={racImageData}
          alt={`${cite.frontmatter.title} - ${cite.frontmatter.summary}`}
        />
        <InnerWrapper>
          <h3>{cite.rawMarkdownBody}</h3>
          <Author>
            {cite.frontmatter.title}
            <AuthorDesc>, {cite.frontmatter.summary}</AuthorDesc>
          </Author>
        </InnerWrapper>
      </Container>
    </EventSectionWrapper>
  );
};

export default CiteSection;
