import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import tw, { styled } from 'twin.macro';

import BaseButton from '../BaseButton';
import MetaPurpleButton from '../MetaPurpleButton';

const OuterSectionWrapper = styled.section`
  // background: linear-gradient(260.69deg, #000000 0%, #0e0e0e 99.91%);
`;

const SectionWrapper = styled.div`
  ${tw`
    p-10 container-2xl
    min-h-halfScreen w-full
    flex flex-row content-center
  `}
  padding-top: 4.75rem;
  padding-bottom: 0;
`;

const ContainerLeft = styled.div`
  ${tw`
  w-full md:w-1/2
  text-white text-xl md:text-2xl text-left
  md:py-0 sm:py-16
  `}
  h2, p, h3, h5 {
    ${tw`
    text-center
    md:text-left
    `}
  }
  h5 {
    ${tw`text-gray40 font-medium`}
    font-size: 1.75rem;
    line-height: 3rem;
    margin-bottom: 2.625rem;
  }
  h3 {
    font-size: 4rem;
    font-weight: 700;
    line-height: 4.875rem;
    margin-bottom: 1.25rem;
  }
`;

const ContainerRight = tw.div`
  hidden md:block my-auto
  w-full md:w-1/2
  ml-20
`;

const ButtonWrapper = tw.div`
  flex flex-wrap flex-col md:inline
  items-center
  mb-10
`;

const heroImgStyle = {
  boxShadow: '0px -20px 72px 10px rgba(221, 212, 241, 0.08)',
  maxWidth: 560,
  marginLeft: 'auto',
};

/**
 * Top section (Hero section).
 *
 * @constructor
 */
const FirstSection = (): React.ReactElement => {
  const { hero, heroImg } = useStaticQuery(
    graphql`
      query {
        hero: markdownRemark(frontmatter: { slug: { eq: "hero" } }) {
          frontmatter {
            title
          }
          rawMarkdownBody
        }
        heroImg: file(relativePath: { eq: "auction-page-1.png" }) {
          childImageSharp {
            gatsbyImageData(
              quality: 90
              placeholder: BLURRED
              layout: FULL_WIDTH
              formats: [AUTO, WEBP, AVIF]
            )
          }
        }
      }
    `,
  );

  const heroImgData = getImage(heroImg);
  return (
    <OuterSectionWrapper>
      <SectionWrapper>
        <ContainerLeft>
          <h3>{hero.frontmatter.title}</h3>
          <h5>{hero.rawMarkdownBody}</h5>
          <ButtonWrapper>
            <MetaPurpleButton>Get in Touch</MetaPurpleButton>
            <BaseButton>Start Building</BaseButton>
          </ButtonWrapper>
        </ContainerLeft>
        <ContainerRight>
          <GatsbyImage
            className="still-vimeo-image position-absolute mt-n10"
            image={heroImgData}
            loading="eager"
            alt="Crypto Kickers Logo"
            style={heroImgStyle}
          />
        </ContainerRight>
      </SectionWrapper>
    </OuterSectionWrapper>
  );
};

export default FirstSection;
